/* eslint-disable camelcase */
/* eslint-disable prefer-promise-reject-errors */
import React, { useState } from 'react';
import { Button, Card, CardBody, CardTitle, FormGroup, Row } from 'reactstrap';

import { Colxx, Separator } from '../../../components/common/CustomBootstrap';
import {
  adminRoot,
  hackathonDataStorageKey,
} from '../../../constants/defaultValues';

import Breadcrumb from '../../../containers/navs/Breadcrumb';
import AppError from '../../../helpers/AppError';
import IntlMessages from '../../../helpers/IntlMessages';

import StateButton from '../../../components/StateButton';
import instance from '../../../helpers/axiosInstance';
import useLocalStorage from '../../../hooks/use-localstorage';
import { getHackathonInfo } from '../../../helpers/Utils';

import 'react-datepicker/dist/react-datepicker.css';
import CustomDateInput from '../../../components/common/CustomDateInput';

const Dates = ({ match }) => {
  const { id } = match.params;

  if (!id) throw new AppError('Please provide a Hackathon ID');
  const info = getHackathonInfo(id);

  const [hackathonData] = useLocalStorage(
    `${hackathonDataStorageKey}_${id}`,
    info
  );

  const {
    application_start,
    application_end,
    hackathon_start,
    hackathon_date,
    submission_date,
    results_date,
  } = hackathonData;

  const [formValues, setFormValues] = useState(() => {
    return {
      application_start: application_start
        ? new Date(application_start)
        : new Date(),
      application_end: application_end ? new Date(application_end) : new Date(),
      hackathon_start: hackathon_start ? new Date(hackathon_start) : new Date(),
      hackathon_date: hackathon_date ? new Date(hackathon_date) : new Date(),
      submission_date: submission_date ? new Date(submission_date) : new Date(),
      results_date: results_date ? new Date(results_date) : new Date(),
    };
  });

  const [dataValues, setData] = useState({
    application_start,
    application_end,
    hackathon_start,
    hackathon_date,
    submission_date,
    results_date,
  });

  console.log(info, formValues);

  const handleChange = (event, key, value) => {
    setFormValues({
      ...formValues,
      [key]: value ? event : event.target.value,
    });

    const d = `${event.getDate()}`.padStart(2, '0');
    const m = `${event.getMonth() + 1}`.padStart(2, '0');
    const y = event.getFullYear();

    setData({
      ...dataValues,
      [key]: value ? `${y}-${m}-${d}` : event.target.value.toJSON(),
    });
  };

  const handleSubmit = async (newData) => {
    return new Promise((success, failed) => {
      const updateProfile = async () => {
        try {
          const response = await instance({
            method: 'POST',
            url: '/updateHackathon',
            data: {
              id,
              ...newData,
            },
          });

          const { data } = response;
          if (data.success) {
            success('Hackathon updated successfully!');
          } else {
            failed('Failed to update hackathon!');
          }
        } catch (error) {
          failed('Failed to update hackathon!');
        }
      };
      updateProfile();
    });
  };

  return (
    <>
      <Row>
        <Colxx xxs="12">
          <Breadcrumb heading="menu.dates" match={match} />
          <Separator className="mb-5" />
        </Colxx>
      </Row>

      <Row className="justify-content-center">
        <Colxx xxs="12" md="10" className="mb-4">
          <Row>
            <Colxx>
              <Card className="mb-4">
                <CardBody>
                  <CardTitle>
                    <div className="lead h4">
                      <IntlMessages id="challenges.hackthon-application-begins" />
                    </div>
                  </CardTitle>
                  <div className="form-group mb-4">
                    <CustomDateInput
                      selectsStart
                      startDate={formValues.application_start}
                      endDate={formValues.application_end}
                      selected={formValues.application_start}
                      onChange={(date) =>
                        handleChange(date, 'application_start', true)
                      }
                      placeholderText="e.g. 04/02/2021"
                    />
                  </div>
                </CardBody>
              </Card>
            </Colxx>

            <Colxx>
              <Card className="mb-4">
                <CardBody>
                  <CardTitle>
                    <div className="lead h4">
                      <IntlMessages id="challenges.hackthon-application-ends" />
                    </div>
                  </CardTitle>
                  <div className="form-group mb-4">
                    <CustomDateInput
                      selectsEnd
                      startDate={formValues.application_start}
                      endDate={formValues.application_end}
                      minDate={formValues.application_start}
                      selected={formValues.application_end}
                      onChange={(date) =>
                        handleChange(date, 'application_end', true)
                      }
                      placeholderText="e.g. 04/02/2021"
                    />
                  </div>
                </CardBody>
              </Card>
            </Colxx>
          </Row>

          <Row>
            <Colxx>
              <Card className="mb-4">
                <CardBody>
                  <CardTitle>
                    <div className="lead h4">
                      <IntlMessages id="challenges.hackthon-hackathon-begins" />
                    </div>
                  </CardTitle>
                  <div className="form-group mb-4">
                    <CustomDateInput
                      selectsStart
                      startDate={formValues.hackathon_start}
                      endDate={formValues.hackathon_date}
                      selected={formValues.hackathon_start}
                      onChange={(date) =>
                        handleChange(date, 'hackathon_start', true)
                      }
                      placeholderText="e.g. 04/02/2021"
                    />
                  </div>
                </CardBody>
              </Card>
            </Colxx>

            <Colxx>
              <Card className="mb-4">
                <CardBody>
                  <CardTitle>
                    <div className="lead h4">
                      <IntlMessages id="challenges.hackthon-hackathon-ends" />
                    </div>
                  </CardTitle>
                  <div className="form-group mb-4">
                    <CustomDateInput
                      selectsEnd
                      startDate={formValues.hackathon_start}
                      endDate={formValues.hackathon_date}
                      selected={formValues.hackathon_date}
                      onChange={(date) =>
                        handleChange(date, 'hackathon_date', true)
                      }
                      placeholderText="e.g. 04/02/2021"
                    />
                  </div>
                </CardBody>
              </Card>
            </Colxx>
          </Row>

          <Row>
            <Colxx>
              <Card className="mb-4">
                <CardBody>
                  <CardTitle>
                    <div className="lead h4">
                      <IntlMessages id="challenges.announcement-results" />
                    </div>
                  </CardTitle>
                  <div className="form-group mb-4">
                    <CustomDateInput
                      selected={formValues.results_date}
                      onChange={(date) =>
                        handleChange(date, 'results_date', true)
                      }
                      placeholderText="e.g. 04/02/2021"
                    />
                  </div>
                </CardBody>
              </Card>
            </Colxx>

            <Colxx>
              <Card className="mb-4">
                <CardBody>
                  <CardTitle>
                    <div className="lead h4">
                      <IntlMessages id="challenges.submission-date" />
                    </div>
                  </CardTitle>
                  <div className="form-group mb-4">
                    <CustomDateInput
                      selected={formValues.submission_date}
                      onChange={(date) =>
                        handleChange(date, 'submission_date', true)
                      }
                      placeholderText="e.g. 04/02/2021"
                    />
                  </div>
                </CardBody>
              </Card>
            </Colxx>
          </Row>

          <Colxx>
            <FormGroup className="text-center">
              <StateButton
                id="submitLinks"
                color="primary"
                type="submit"
                onClick={() => handleSubmit(dataValues)}
              >
                <IntlMessages id="forms.submit" />
              </StateButton>
            </FormGroup>
          </Colxx>
        </Colxx>
      </Row>

      <Row>
        <Colxx>
          <FormGroup className="text-center">
            <Button
              color="primary"
              className="m-3"
              href={`${adminRoot}/challenges/links/${id}`}
            >
              <IntlMessages id="forms.previous" />
            </Button>
            <Button
              color="primary"
              className="m-3"
              href={`${adminRoot}/challenges/prizes/${id}`}
            >
              <IntlMessages id="forms.next" />
            </Button>
          </FormGroup>
        </Colxx>
      </Row>
    </>
  );
};

export default Dates;
